h1, h2, h3 {
  display: inline-block;
  vertical-align: sub;
}

.title, .ender {
  display: inline-block;
}

section {
  padding-top: 15px;
  padding-bottom: 15px;
}

section:not(.active) > .decor {

}

section.active > .decor {
  color: red;
}

section.active > .title {
  padding-left: 25px;
}

section > div > canvas {
  width: 500px;
  height: 350px;
}

header {
  display: block;
}

footer {
  position: fixed;
  bottom: 0;
  right: 20px;
}

.section-body {
  padding-left: 25px;
  padding-top: 20px;
  padding-bottom: 15px;
  height: fit-content;
}

span.section-body {
  padding-left: unset;
}

#canvas {
  width: 400px;
  height: 400px;
  border: 1px dashed orange;
}

#display-container {
  position: relative;
  width: 400px;
  height: 400px;
  overflow: hidden;
  margin-bottom: 20px;
}

#display {
  display: inline-block;
  position: absolute;
  width: 400px;
  height: 400px;
  overflow: hidden;
}

.text-label {
  position: absolute;  /* let us position them inside the container */
  left: 0;             /* make their default position the top left of the container */
  top: 0;
  cursor: pointer;     /* change the cursor to a hand when over us */
  user-select: none;   /* don't let the text get selected */
  color: black;
  font-size: 12px;
  line-height: normal;
  font-weight: normal;
  background-color: rgba(238, 224, 224, 0.5);
  z-index: 120;
  border: 1px dashed cornflowerblue;
  margin: 0;
  padding: 0;
}

canvas {
  width: 400px;
  height: 400px;
}

a {
  color: lightskyblue;
}

